import { Model } from "../util/checkout/model";
import { View } from "../util/checkout/view";
import { Controller } from "../util/checkout/controller";
import "cleave.js/dist/addons/cleave-phone.us"

window.addEventListener("load", () => {
  const model = new Model();
  const view = new View();
  const controller = new Controller(model, view);
  
  const cleaveCreditCard = new Cleave('#Payment-Info-Card-Number', {
    creditCard: true
  });
  const monthYear = new Cleave('#Payment-Info-Card-Expiration-Date', {
    date: true,
    datePattern: ['m', 'y']
  });
  const phone = new Cleave('#customer-info-phone', {
    phone: true,
    phoneRegionCode: 'us'
  })
});

