class FormUtility {
  constructor() {}
  // Args: fields - array of objects
  //		{
  //			field: DOM Obj -> the input html obj
  //			name: String -> name to which to address the field by
  //			checks: [String] -> checks to perform on this field
  //
  //		}
  // Valid checks - not_empty, min_length, email, card_num

  verify(fields) {
    for (var i = 0; i < fields.length; i++) {
      var obj = fields[i];
      var input = obj.field;
      var name = obj.name;
      const value = obj.dataValue ? input.dataset.value : input.value;

      for (var j = 0; j < obj.checks.length; j++) {
        var check = obj.checks[j];

        if (check === "not_empty") {
          if (!value || !value.trim()) {
            return { status: "invalid", message: name + " is required.", input: input };
          }
        }

        if (check === "min_length") {
          if (value.trim().length < 3) {
            return { status: "invalid", message: name + " must be at least 3 characters.", input: input };
          }
        }

        if (check === "card_num") {
          if (!this.cardNumberValidation(value)) {
            return { status: "invalid", message: name + " is not valid.", input: input };
          }
        }

        if (check === "email") {
          if (!this.validateEmail(value)) {
            return { status: "invalid", message: name + " is not valid.", input: input };
          }
        }
      }
    }
    return { status: "valid" };
  }

  cardNumberValidation(value) {
    //	add validation here
    value = value.replace(/\D/g, "");

    //check for valid length - should be between 13-19 digits
    if (value.length < 13 || value.length > 19) {
      return false;
    }

    //Pop off the last digit
    var lastDigit = value.slice(-1);

    //Get the value without the last digit
    value = value.slice(0, -1);

    //Reverse the string
    value = value.split("").reverse(); //.join("");

    var total = 0;
    for (var index = 0; index < value.length; ++index) {
      //Double every other digit
      if (index % 2 == 0) {
        value[index] = value[index] * 2;
      }

      //Subtract 9 from each value over 9
      if (value[index] > 9) {
        value[index] = value[index] - 9;
      }

      //Add up all the numbers
      total = Number(total) + Number(value[index]);
    }

    //Muliply the total by 9
    total = Number(total) * 9;

    //Value modulo 10 should equal the lastDigit variable if card is the correct format
    return total % 10 == lastDigit;
  }

  validateEmail(email) {
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }
}

export class Model {
  formUtil = new FormUtility();

  guestBillingAddressAutocompleteData;
  guestShippingAddressAutocompleteData;
  addAddressAutocompleteData;
  total = 0;
  user; // set in init
  userMetadata;
  db; // set in init
  usersTableIdentifier = "fcbhUsers";
  cartId = "fcbh_cart";

  //	callbacks
  //self.onAddressDidUpdate;
  onDataUpdate;

  getItemsData() {
    var obj = {
      items: [],
      total: 0,
    };

    var items = this.getCart();
    for (var i = 0; i < items.length; i++) {
      var item = items[i];
      obj.total += item.subtotal;
      obj.items.push(item);
    }
    obj.total = obj.total.toFixed(2);
    return obj;
  }

  getCart() {
    return JSON.parse(localStorage.getItem(this.cartId) || "{}") || [];
  }

  setCart(cart) {
    localStorage.setItem(this.cartId, JSON.stringify(cart));
  }

  getUserData(id) {
    var self = this;
    return new Promise(function (resolve, reject) {
      FCBH.firebaseDb
        .collection(self.usersTableIdentifier)
        .doc(id)
        .get()
        .then(function (doc) {
          resolve(doc.exists);
        })
        .catch(function (error) {
          reject();
        });
    });
  }

  createNewUser(user) {
    console.log("create new user", user);
    var self = this;
    return new Promise(function (resolve, reject) {
      FCBH.firebaseDb
        .collection(self.usersTableIdentifier)
        .doc(user.uid)
        .set({
          email: user.email,
          joined_date: user.metadata.creationTime,
          name: user.displayName,
          photo: user.photoURL,
        })
        .then(function () {
          resolve();
        })
        .catch(function (error) {
          reject();
        });
    });
  }

  listenForUpdates(id) {
    var self = this;
    FCBH.firebaseDb
      .collection(self.usersTableIdentifier)
      .doc(id)
      .onSnapshot(function (doc) {
        var data = doc.data();
        //console.log(data);
        //self.user = data;
        //self.onAddressDidUpdate(data.addresses);
        self.onDataUpdate(data);
      });
  }

  saveAddressToFirebase(address = []) {
    var self = this;

    var addresses = self.userMetadata.addresses || [];
    addresses.push(...address);

    return new Promise(function (resolve, reject) {
      FCBH.firebaseDb
        .collection(self.usersTableIdentifier)
        .doc(self.user.uid)
        .update({ addresses: addresses })
        .then(function () {
          resolve();
        })
        .catch(function (e) {
          reject(e);
        });
    });
  }
  saveOrder(order) {
    return FCBH.axiosInstance.post('https://pay.faithcomesbyhearing.com/index.php/store/store_transaction', order);
  }

  extractAddress(address) {
    if (!address) {
      return;
    }

    var obj = {
      address: "",
      city: "",
      state: "",
      zip: "",
      country: "",
    };

    for (var i = 0; i < address["address_components"].length; i++) {
      var item = address["address_components"][i];

      for (var j = 0; j < item["types"].length; j++) {
        switch (item["types"][j]) {
          case "street_number":
            obj.address = item["short_name"];
            break;
          case "route":
            obj.address += " " + item["short_name"];
            break;
          case "locality":
            obj.city = item["short_name"];
            break;
          case "administrative_area_level_1":
            obj.state = item["long_name"];
            break;
          case "country":
            obj.country = item["long_name"];
            break;
          case "postal_code":
            obj.zip = item["long_name"];
            break;
          default:
        }
      }
    }
    return obj;
  }

  clearCart() {
    this.setCart([]);
  }
}
