export class View {
  guestBillingAddressField = document.querySelector("#Billing-Address-Street");
  guestShippingAddressField = document.querySelector("#Shipping-Address-Street");

  reviewDiv = document.querySelector(".order-item-list");
  subtotal = document.querySelector(".w-commerce-commercecheckoutsummarylineitem:nth-child(1) > div:nth-child(2)");
  total = document.querySelector(".w-commerce-commercecheckoutsummarylineitem:nth-child(3) > div:nth-child(2)");

  guestSelectedShippingAddress = document.querySelector("#Shipping-Address-Street");
  guestSelectedBillingAddress = document.querySelector("#Billing-Address-Street");

  email = document.querySelector("#customer-info-email");
  emailLoggedInUser = document.getElementById("customer-info-email");

  phoneNumber = document.querySelector("#customer-info-phone");

  userFullName = document.querySelector("#shipping-address-name");
  billingFullName = document.querySelector("#Billing-Address-Name");
  shippingAddressTwo = document.querySelector("#Shipping-Address-Street-2");
  billingAddressTwo = document.querySelector("#Billing-Address-Street-2");
  shippingAddressCity = document.querySelector("#Shipping-Address-City");
  billingAddressCity = document.querySelector("#Billing-Address-City");
  shippingAddressState = document.querySelector("#Shipping-Address-State");
  billingAddressState = document.querySelector("#Billing-Address-State");
  shippingAddressPostalCode = document.querySelector("#Shipping-Address-Postal-Code");
  billingAddressPostalCode = document.querySelector("#Billing-Address-Postal-Code");

  shippingAddressCountryContainer = document.querySelector(".shipping-address #shipping-country");
  shippingAddressCountryValue = () => document.querySelector("#w-dropdown-toggle-0 > div");
  shippingAddressCountryList = () => document.querySelector("#w-dropdown-list-0");

  billingAddressCountryContainer = document.querySelector(".billing-address #shipping-country");
  billingAddressCountryValue = () => document.querySelector("#w-dropdown-toggle-1 > div");
  billingAddressCountryList = () => document.querySelector("#w-dropdown-list-1");

  billingAddressContainer = document.querySelector(".billing-address");

  billingEqualsShippingButton = document.querySelector("#same-as-shipping");
  cardNumberInput = document.getElementById("Payment-Info-Card-Number");
  cardExpirationInput = document.getElementById("Payment-Info-Card-Expiration-Date");
  cardCVCInput = document.getElementById("Payment-Info-Card-CVV");

  addressListContainer = document.querySelector("#address-container");
  selectAddressText = document.querySelector("#select-address-text");
  addNewAddressText = document.querySelector("#add-new-address-text");

  form = document.querySelector("#email-form");

  // not real elements
  addAddressField = document.querySelector(".footer-legal");
  guestElements = document.querySelectorAll(".footer-legal1");
  selectedShippingAddress = document.querySelector(".footer-legal");
  selectedBillingAddress = document.querySelector(".footer-legal");
  popup = document.querySelector(".footer-legal");
  overlay = document.querySelector(".footer-legal");
  popupTitle = document.querySelector(".footer-legal");
  popupAddressListBlock = document.querySelector(".footer-legal");
  popupAddNewAddressButton = document.querySelector(".footer-legal");
  popupAddNewAddressFields = document.querySelector(".footer-legal");
  updateAddressesButton = document.querySelector(".footer-legal");
  addNewAddressNameField = document.querySelector(".footer-legal");
  addNewAddressTwoField = document.querySelector(".footer-legal");
  verifyNewAddressBlock = document.querySelector(".footer-legal");
  changeAddressBlock = document.querySelector(".footer-legal");
  verifyAddressButton = document.querySelector(".footer-legal");
  closePopupButton = document.querySelector(".footer-legal");
  verificationBox = document.querySelector("verification-box");

  phone = document.querySelector(".footer-legal");
  addNewAddressPhone = document.querySelector(".footer-legal");

  onChangeSelectedAddressButtonClicked;
  onSelectedAddressChange;
  onReviewItemQuantityChange;
  onReviewItemDelete;

  onAddressSelectedForBillingVerify;
  onAddressSelectedForShippingVerify;
  onVerificationCancelled;

  constructor() {
    this.overlay.onclick = () => {
      this.closePopup();
      this.verificationBox.style.display = "none";
    };
    setTimeout(() => {
      this.processCountryList();
    }, 100);
  }
  //	Callbacks
  onAddNewAddressClicked(field) {
    this.openPopup();
  }

  /**
   * Show/hide address list items
   */
  toogleAddressList() {
    this.addressListContainer.style.display = this.addressListContainer.style.display === "none" ? "block" : "none";
    this.selectAddressText.style.display = this.selectAddressText.style.display === "none" ? "block" : "none";
    this.addNewAddressText.style.display = this.addNewAddressText.style.display === "none" ? "block" : "none";
  }

  /**
   * Renders a list of addresses
   * @param {any[]} addresses
   */
  renderAddressList(addresses = []) {
    while (this.addressListContainer.lastChild) {
      this.addressListContainer.removeChild(this.addressListContainer.lastChild);
    }
    const nodes = addresses.map((address) => {
      return `
      <label class="default-form__radio-field radio-address w-radio">
        <input type="radio" name="select-shipping-address"  data-name="select-shipping-address" value="${
          address.id
        }" class="w-form-formradioinput default-form__radio w-radio-input" />
        <span class="default-form__label default-form__label--radio w-form-label">
          ${address.address.trim()}
          <br>
          ${address.address_two.trim()}
          <br>
          ${address.city}, ${address.state} ${address.zip}
        </span>
      </label>
      `;
    });
    this.addressListContainer.innerHTML = nodes.join("");
    this.appendRadioControls(this.addressListContainer);
  }

  /**
   * Adds control management to the radio inputs in container
   * @param {Element} container
   */
  appendRadioControls(container) {
    let currentlyChecked = null;
    for (const radio of container.querySelectorAll('input[type="radio"]')) {
      radio.addEventListener("click", (ev) => {
        if (ev.target.checked && ev.target === currentlyChecked) {
          ev.target.checked = false;
          currentlyChecked = null;
          this.toggleShippingAddress(false);
        } else if (ev.target.checked) {
          currentlyChecked = ev.target;
          this.toggleShippingAddress(true);
        }
      });
    }
  }

  /**
   * Enables/disables the shipping address fields
   * @param {Boolean} disabled
   */
  toggleShippingAddress(disabled = true) {
    const fields = [
      this.guestShippingAddressField,
      this.userFullName,
      this.shippingAddressTwo,
      this.shippingAddressCity,
      this.shippingAddressPostalCode,
      this.shippingAddressState,
    ];
    fields.forEach((field) => {
      field.required = !disabled;
      field.disabled = disabled;
    });
  }

  /**
   * Gets the currently checked shipping address, null if none
   */
  getCheckedShippingAddress() {
    return document.querySelector('input[name="select-shipping-address"]:checked');
  }

  /**
   * Renders the country list for shipping and billing addresses
   */
  processCountryList() {
    const shippingNode = document.createElement("a");
    shippingNode.href = "#";
    shippingNode.classList.add("dropdown-link", "w-dropdown-link");
    shippingNode.dataset.value = "USA";
    shippingNode.textContent = "United States";

    const billingNode = shippingNode.cloneNode(true);

    shippingNode.addEventListener("click", (ev) => {
      this.shippingAddressCountryValue().textContent = ev.target.textContent;
      this.shippingAddressCountryValue().dataset.value = ev.target.dataset.value;
      this.shippingAddressCountryValue().dispatchEvent(
        new MouseEvent("mouseup", {
          view: window,
          bubbles: true,
          cancelable: false,
        })
      );
    });

    this.shippingAddressCountryValue().textContent = shippingNode.textContent;
    this.shippingAddressCountryValue().dataset.value = shippingNode.dataset.value;

    billingNode.addEventListener("click", (ev) => {
      this.billingAddressCountryValue().textContent = ev.target.textContent;
      this.billingAddressCountryValue().dataset.value = ev.target.dataset.value;
      this.billingAddressCountryValue().dispatchEvent(
        new MouseEvent("mouseup", {
          view: window,
          bubbles: true,
          cancelable: false,
        })
      );
    });

    this.billingAddressCountryValue().textContent = billingNode.textContent;
    this.billingAddressCountryValue().dataset.value = billingNode.dataset.value;
    this.cleanAndRenderCountry(this.shippingAddressCountryList(), [shippingNode]);
    this.cleanAndRenderCountry(this.billingAddressCountryList(), [billingNode]);
  }

  /**
   * Cleans all child elements from country list and adds new options
   * @param {Element} countryBaseNode
   * @param {Node[]} countryOptions
   */
  cleanAndRenderCountry(countryBaseNode, countryOptions = []) {
    while (countryBaseNode.lastChild) {
      countryBaseNode.removeChild(countryBaseNode.lastChild);
    }
    countryBaseNode.append(...countryOptions);
  }

  //	Sets items that are being reviewed

  refreshReviewItems(data) {
    const items = data.items;
    this.reviewDiv.innerHTML = "";
    const nodes = items.map((item, i) => {
      console.log(item)
      return `
      <div class="order-item" data-id="${i}" data-qty="${item.quantity}">
        <img src="https://cdn.faithcomesbyhearing.com/resources/fcbh/img/store/products/${
          item.thumbnail
        }" alt="" class="order-item-image loaded" loading="lazy" data-ll-status="loaded">
        <div class="order-item-info">
          <div class="order-item-title">${item.title}</div>
          <div class="txt-sm txt-qty">
          ${item.version}
          </div>
          <div class="txt-sm txt-qty">
          ${item.language}
          </div>
          <div class="order-item-qty">
            <div class="txt-sm txt-qty">Quantity: </div>
            <div class="txt-sm txt-qty">${item.quantity}</div>
          </div>
        </div>
        <div class="order-item-price txt-md">$${item.subtotal.toFixed(2)}USD</div>
      </div>
      `;
    });

    this.reviewDiv.innerHTML = nodes.join("");

    this.subtotal.innerHTML = "$" + data.total;
    this.total.innerHTML = "$" + data.total;
  }

  setGuestElements(user) {
    var self = this;
    if (user) {
      for (var i = 0; i < self.guestElements.length; i++) {
        self.guestElements[i].dataset.guest = "false";
      }
    }
  }

  /*
Args: 
  id -> if id is passed, it looks for the address with that id.
  field -> either "billing" or "shipping". Lets function know which address to change.
*/

  displaySelectedAddresses(addresses, id, field) {
    var self = this;
    id = id || false;

    if (addresses && addresses.length > 0) {
      var address = addresses[0]; // defaults to first value
      for (var i = 0; i < addresses.length; i++) {
        if (id && addresses[i].id == id) {
          address = addresses[i];
          break;
        }
      }

      var html =
        "<p>" +
        address.full_name +
        "</p><p>" +
        address.address +
        "</p><p>" +
        address.city +
        ", " +
        address.state +
        " " +
        address.zip +
        " " +
        address.country +
        "</p><p><span class='hover change-address-button' style='color: #9e1e35;text-decoration:underline'>Change</span></p>";

      if (field) {
        var div = field === "billing" ? self.selectedBillingAddress : self.selectedShippingAddress;
        div.innerHTML = html;
        div.dataset.address = address.id;
      } else {
        self.selectedShippingAddress.innerHTML = self.selectedBillingAddress.innerHTML = html;
        self.selectedShippingAddress.dataset.address = self.selectedBillingAddress.dataset.address = address.id;
      }

      var buttons = document.querySelectorAll(".change-address-button");

      for (var i = 0; i < buttons.length; i++) {
        buttons[i].onclick = self.onChangeSelectedAddressButtonClicked;
      }
    } else {
      self.selectedShippingAddress.innerHTML = self.selectedBillingAddress.innerHTML =
        "<p><span class='hover' style='text-decoration:underline;color:#9e1e35'>Add a new address</span></p>";

      self.selectedShippingAddress.querySelector("span").onclick = function () {
        self.onAddNewAddressClicked("shipping");
      };

      self.selectedBillingAddress.querySelector("span").onclick = function () {
        self.onAddNewAddressClicked("billing");
      };
    }
  }

  showVerifyAddressBox(addresses) {
    var self = this;
    var list = document.getElementById("verify-address-list");
    list.innerHTML = "";

    if (addresses && addresses.length > 0) {
      for (var i = 0; i < addresses.length; i++) {
        list.innerHTML +=
          "<div><label><input value='" +
          addresses[i].place_id +
          "' name='verify-address' type='radio' /><section></section>" +
          addresses[i].description +
          "</label></div>";
      }
    }
  }
  openPopup(field, view) {
    view = view || "add-new";

    this.popup.dataset.field = field || "shipping";

    this.popup.dataset.prev = this.popup.dataset.view = ""; // reset stored popup views
    this.changePopupView(view);

    if (this.popup.style.right !== "0px" || this.popup.style.right !== "0") {
      this.overlay.style.display = "block";
      this.popup.style.right = "0px";
    }
  }

  //	Args - view: "add-new", "change", "verify"
  changePopupView(view) {
    this.changeAddressBlock.style.display = "none";
    this.verifyNewAddressBlock.style.display = "none";
    this.popupAddNewAddressFields.style.display = "none";

    view = view || "add-new"; // default to add-new

    this.popup.dataset.prev = this.popup.dataset.view || "";
    this.popup.dataset.view = view;

    if (view === "add-new") {
      this.popupAddNewAddressFields.style.display = "block";
    } else if (view === "change") {
      this.changeAddressBlock.style.display = "block";
    } else if (view === "verify") {
      this.verifyNewAddressBlock.style.display = "block";
    }
  }
  backToPreviousPopupView() {
    if (this.popup.dataset.prev) {
      this.changePopupView(this.popup.dataset.prev);
    }
  }
  closePopup() {
    this.popup.style.right = "-300px";

    setTimeout(function () {
      this.overlay.style.display = "none";
    }, 510);
  }

  setChangeAddressPopupAddressList(addresses) {
    var self = this;
    self.popupAddressListBlock.innerHTML = "";
    if (!addresses) {
      return;
    }

    var selectedAddressId;
    var type = self.popup.dataset.field || "billing"; // shipping or billing
    var d = document.getElementById("selected-" + type + "-address");
    if (d) {
      selectedAddressId = d.dataset.address;
    }

    for (var i = 0; i < addresses.length; i++) {
      var address = addresses[i];
      var checked = address.id === selectedAddressId || (i === 0 && !selectedAddressId) ? "checked" : "";
      var addressTwo = addresses[i].address_two ? addresses[i].address_two + "<br>" : "";
      // 				var html = "<div><label><input type='radio' name='address' data-id=" + address.id + " " + checked + "/><section></section>" + address.full_name + "<br>" + address.address + "<br>" + addressTwo + address.city + ", " + address.state + " " + address.zip + "</label><br><span style='width: 100%;display: inline-block;text-align: right;'><img src='https://cdn.faithcomesbyhearing.com/resources/fcbh/img/store/delete.svg'></span></div>";
      var html =
        "<div><label><input type='radio' name='address' data-id=" +
        address.id +
        " " +
        checked +
        "/><section></section>" +
        address.full_name +
        "<br>" +
        address.address +
        "<br>" +
        addressTwo +
        address.city +
        ", " +
        address.state +
        " " +
        address.zip +
        "</label></div>";
      this.popupAddressListBlock.innerHTML += html;
    }

    //	add event listener to pick up changes
    var radios = document.querySelectorAll("input[name=address]");
    for (var i = 0; i < radios.length; i++) {
      radios[i].onclick = function () {
        self.onSelectedAddressChange(this.dataset.id);
      };
    }
  }

  toggleBillingAddressSection(div) {
    var isClosed = div.style.display === "none";
    div.style.display = isClosed ? "block" : "none";
    div.querySelectorAll("input").forEach((input) => {
      input.required = isClosed;
    });
  }

  setPopupSelectedAddress(id) {
    var inputs = document.querySelectorAll("input[name=address]");
    for (var i = 0; i < inputs.length; i++) {
      if (inputs[i].dataset.id == id) {
        inputs[i].checked = true;
        break;
      }
    }
  }

  startLoader(div) {
    div.value = div.dataset.wait;
    div.textContent = div.dataset.wait;
  }

  showAddressVerification(type, addresses, extract) {
    var self = this;
    type = type || "shipping";
    /*
  if (!addresses || addresses.length == 0) {
    return false;
  }
*/

    self.overlay.style.display = "block";
    self.verificationBox.style.display = "block";

    var html = "<h3>Please verify your " + type + " address.</h3><div class='verification-addresses'>";

    if (!addresses || addresses.length == 0) {
      self.verificationBox.innerHTML =
        html +
        "<br><br><p style='text-align:center'>No addresses could be found. Please search a different address.</p><br><p id='address-verification-cancel' style='text-decoration:underline;text-align:center'>Cancel</p>";

      document.getElementById("address-verification-cancel").onclick = function () {
        self.closeAddressVerificationBox();
      };
      return;
    }

    for (var i = 0; i < addresses.length; i++) {
      var address = addresses[i];
      html +=
        "<div class='address-verification-" +
        type +
        "'><label><input type='radio' name='address' data-id='" +
        address.place_id +
        "' /><section></section>" +
        address.description +
        "</label></div>";
    }
    html +=
      "</div><div style='text-align:center'><button id='" +
      type +
      "-verification-done-button' type='button' class='store-button red'>Done</button><p id='address-verification-cancel' style='text-decoration:underline'>Cancel</p></div>";
    self.verificationBox.innerHTML = html;

    document.getElementById(type + "-verification-done-button").onclick = function () {
      var placeId;
      var inputs = document.querySelectorAll(".address-verification-" + type + " input");
      for (var i = 0; i < inputs.length; i++) {
        if (inputs[i].checked) {
          placeId = inputs[i].dataset.id;
          break;
        }
      }

      if (!placeId) {
        //	TODO: show error message
        return;
      }

      if (type === "shipping") {
        self.onAddressSelectedForShippingVerify(placeId);
      } else if (type === "billing") {
        self.onAddressSelectedForBillingVerify(placeId);
      }
    };

    document.getElementById("address-verification-cancel").onclick = function () {
      self.closeAddressVerificationBox();
    };
  }

  closeAddressVerificationBox() {
    this.overlay.style.display = "none";
    this.verificationBox.style.display = "none";
  }

  /******************************
Helpers
******************************/
  attachListeners(name, array, callback) {
    for (var i = 0; i < array.length; i++) {
      var item = array[i];
      item.addEventListener(name, callback);
    }
  }
}
