import { isNil } from 'lodash';
export class Controller {
  /**
   * @type {import('./model').Model}
   */
  model = null;
  /**
   * @type {import('./view').View}
   */
  view = null;

  /**
   *
   * @param {import('./model').Model} model
   * @param {import('./view').View} view
   */
  constructor(model, view) {
    this.model = model;
    this.view = view;
    this.view.toogleAddressList(); // Start by hiding the address list items
    this.createViewCallbacks();
    this.onViewDidLoad();
  }

  /******************************
  Callbacks
  ******************************/
  createViewCallbacks() {
    const self = this;
    //
    // Runs whenever a snapshot comes in from firebase.
    // Args - "userData" is the data that comes from firebase.
    //
    self.model.onDataUpdate = function (userData) {
      self.model.userMetadata = userData;
      self.view.email.value = userData.email;
      self.view.email.disabled = true;
      console.log(userData);
      if (userData.addresses && userData.addresses.length > 0) {
        self.view.toogleAddressList();
        self.view.renderAddressList(userData.addresses);
      }
    };

    //
    // Runs when user saves a new address.
    // Verifies all fields are complete.
    // Validates address if user didn't use a Google address.
    // Saves to firebase.
    //
    self.view.updateAddressesButton.onclick = function () {
      var error = this.parentElement.previousElementSibling;
      error.innerHTML = "";

      var nameField = self.view.addNewAddressNameField;
      var addressField = self.view.addAddressField;
      var addressTwoField = self.view.addNewAddressTwoField;
      var phoneField = self.view.addNewAddressPhone;

      //	Verify form fields
      var message = self.model.formUtil.verify([
        {
          field: nameField,
          name: "Full Name",
          checks: ["not_empty", "min_length"],
        },
        {
          field: addressField,
          name: "Address",
          checks: ["not_empty"],
        },
        {
          field: phoneField,
          name: "Phone",
          checks: ["not_empty"],
        },
      ]);

      if (message.status === "invalid") {
        error.innerHTML = message.message;
        return;
      }

      //	Save address
      if (addressField.dataset.googleAddress === "yes") {
        //	user is using google autocomplete address
        //	save address
        var place = self.model.addAddressAutocompleteData.getPlace();
        var address = self.model.extractAddress(place);
        address.full_name = nameField.value;
        address.phone = phoneField.value;
        address.id = self.guid();
        if (addressTwoField.value.trim()) {
          address.address_two = addressTwoField.value;
        }
        self.model
          .saveAddressToFirebase(address)
          .then(function () {
            self.view.changePopupView("change");
            self.view.popup.dataset.prev = "";
          })
          .catch(function (e) {
            console.log(e);
            console.log("failed to save address to firebase");
          });
      } else {
        //	user typed in their own address
        //	search for a match within google's system
        var _address = addressField.value;
        //	search address
        self.model
          .verifyAddress(_address)
          .then(function (data) {
            self.view.changePopupView("verify");
            self.view.showVerifyAddressBox(data);

            // callback that runs when user selects a predicted address
            self.view.verifyAddressButton.onclick = function () {
              var selected = document.querySelector("input[name=verify-address]:checked");
              if (selected) {
                self.model
                  .getGooglePlaceById(selected.value)
                  .then(function (place) {
                    var formattedAddress = self.model.extractAddress(place);
                    //	save address

                    formattedAddress.full_name = nameField.value;
                    formattedAddress.phone = phoneField.value;
                    formattedAddress.id = self.guid();
                    if (addressTwoField.value.trim()) {
                      formattedAddress.address_two = addressTwoField.value;
                    }
                    self.model
                      .saveAddressToFirebase(formattedAddress)
                      .then(function () {
                        self.view.changePopupView("change");
                        self.view.popup.dataset.prev = "";
                      })
                      .catch(function (e) {
                        console.log(e);
                        console.log("failed to save address to firebase");
                      });
                  })
                  .catch(function (e) {
                    console.log(e);
                  });
              } else {
                // nothing was selected
              }
            };
          })
          .catch(function (e) {
            console.log(e);
          });
      }
    };

    //
    // Ran when the user clicks "change" to change the selected address.
    // Only runs for logged in users
    //
    self.view.onChangeSelectedAddressButtonClicked = function (e) {
      var type = e.target.parentElement.parentElement.dataset.id;
      var addressId = e.target.parentElement.parentElement.dataset.address;

      self.view.setPopupSelectedAddress(addressId);
      self.view.openPopup(type, "change");
    };

    self.view.popupAddNewAddressButton.onclick = function () {
      self.view.changePopupView();
    };

    self.view.closePopupButton.onclick = function () {
      self.view.closePopup();
    };

    self.view.onSelectedAddressChange = function (id) {
      var field = self.view.popup.dataset.field; // get the field (billing or shipping) to change
      self.view.displaySelectedAddresses(self.model.userMetadata.addresses, id, field);
    };

    self.view.billingEqualsShippingButton.onclick = function () {
      var container = self.view.billingAddressContainer;
      self.view.toggleBillingAddressSection(container);
    };

    self.view.onReviewItemQuantityChange = function (e) {
      var newQuantity = e.target.value;
      var oldQuantity = e.target.parentElement.parentElement.dataset.qty;
      var updateButton = e.target.nextElementSibling;

      updateButton.style.color = newQuantity && newQuantity > 0 && newQuantity != oldQuantity ? "#9e1e35" : "#cccccc";
      updateButton.dataset.enabled = newQuantity && newQuantity > 0 && newQuantity != oldQuantity ? 1 : 0;
    };

    self.view.onReviewItemQuantityLosingFocus = function (e) {
      var div = e.target.parentElement.parentElement;
      var updateButton = e.target.nextElementSibling;

      if (div.dataset.qty != e.target.value) {
        e.target.value = div.dataset.qty;
        updateButton.style.color = "#cccccc";
        updateButton.dataset.enabled = 0;
      }
    };

    self.view.onReviewItemUpdateQuantity = function (e) {
      if (e.target.dataset.enabled == 0) {
        return;
      }
      //	update cart
      var cart = self.model.getCart();
      var index = e.target.parentElement.parentElement.dataset.id;
      var newQuantity = e.target.previousElementSibling.value;

      if (cart.length > index && newQuantity) {
        cart[index].quantity = parseInt(newQuantity);
        cart[index].subtotal = parseInt(newQuantity) * parseFloat(cart[index].unit_price);
        self.model.setCart(cart);
      }
      //	refresh items
      self.view.refreshReviewItems(self.model.getItemsData());
    };

    self.view.onReviewItemDelete = function (e) {
      var index = this.dataset.index;
      var cart = self.model.getCart();
      cart.splice(index, 1);
      self.model.setCart(cart);
      self.view.refreshReviewItems(self.model.getItemsData());
    };

    var backButtons = document.getElementsByClassName("popup-back-button");
    for (var i = 0; i < backButtons.length; i++) {
      backButtons[i].onclick = function (e) {
        self.view.backToPreviousPopupView();
      };
    }

    var checkoutButtons = document.querySelectorAll('#email-form input[type="submit"]');
    //	checkout
    for (var i = 0; i < checkoutButtons.length; i++) {
      checkoutButtons[i].onclick = function (ev) {
        ev.preventDefault();
        ev.stopPropagation();
        ev.stopImmediatePropagation();
        //	reset error text for both checkbuttons
        self.placeOrder(this);
      };
    }
  }

  onViewDidLoad() {
    /******************************
    Init
    ******************************/

    this.avoidForm();

    var self = this;

    self.model.db = FCBH.firebaseDb;

    // 				window.initialize = self.initialize.bind(self);
    self.view.refreshReviewItems(self.model.getItemsData());

    //	firebase callback and is immediately invoked somehow?
    window.firebase.auth().onAuthStateChanged(function (user) {
      self.model.user = user;
      self.view.setGuestElements(user);
      if (user) {
        self.model
          .getUserData(user.uid)
          .then(function (exists) {
            if (exists) {
              self.model.listenForUpdates(user.uid);
            } else {
              self.model
                .createNewUser(user)
                .then(function () {
                  self.model.listenForUpdates(user.uid);
                })
                .catch(function (e) {
                  console.log(e);
                  console.log("problem occured creating new user");
                });
            }
          })
          .catch(function (e) {
            console.log("error occurred - catch");
            console.log(e);
            // 				alert("An error occurred retrieving your information. Please try again later");
          });
      }
    });
  }
  /******************************
  Helpers
  ******************************/
  guid() {
    function s4() {
      return Math.floor((1 + Math.random()) * 0x10000)
        .toString(16)
        .substring(1);
    }
    return s4() + s4() + "-" + s4() + "-" + s4() + "-" + s4() + "-" + s4() + s4() + s4();
  }

  addressById(addresses, id) {
    if (!id || !addresses || addresses.length === 0) {
      return false;
    }
    for (var i = 0; i < addresses.length; i++) {
      if (addresses[i].id == id) {
        return addresses[i];
      }
    }
    return false;
  }

  showError(message) {
    const errorDiv = document.querySelector(".w-form-fail");
    errorDiv.style.display = "block";
    window.scrollTo(0, errorDiv.offsetTop - 150);
    errorDiv.textContent = message;
  }

  hideError() {
    const errorDiv = document.querySelector(".w-form-fail");
    errorDiv.style.display = "none";
  }

  async fetchTokens() {
    const { data } = await FCBH.axiosInstance.get(`${FCBH.BASE_URL}donate/tks`);
    const csrf_token = data.csrf_token;
    const csrf_token_2 = data.csrf2_token;
    return { csrf_token, csrf_token_2 };
  }

  avoidForm() {
    this.view.form.addEventListener("submit", (ev) => {
      ev.preventDefault();
      ev.stopPropagation();
      ev.stopImmediatePropagation();
    });
  }

  async placeOrder(el) {
    //	clear error messages
    this.hideError();
    var self = this;

    if (self.model.getCart().length == 0) {
      this.showError("No items in cart");
      return;
    }
    
    //	Verify form fields
    var fields = [];
    var billingEqualsShipping = self.view.billingEqualsShippingButton.checked;
    const shippingAddressValue = self.view.getCheckedShippingAddress();
    console.log("data", shippingAddressValue);
    if (!shippingAddressValue) {
      fields = fields.concat([
        {
          field: self.view.userFullName,
          name: "Full Name",
          checks: ["not_empty", "min_length"],
        },
        {
          field: self.view.email,
          name: "Email",
          checks: ["not_empty", "email"],
        },
        {
          field: self.view.phoneNumber,
          name: "Phone number",
          checks: ["not_empty"],
        },
        {
          field: self.view.guestShippingAddressField,
          name: "Shipping Address",
          checks: ["not_empty"],
        },
        {
          field: self.view.shippingAddressCity,
          name: "Shipping Address City",
          checks: ["not_empty"],
        },
        {
          field: self.view.shippingAddressState,
          name: "Shipping Address State",
          checks: ["not_empty"],
        },
        {
          field: self.view.shippingAddressPostalCode,
          name: "Shipping Address Postal Code",
          checks: ["not_empty"],
        },
        {
          field: self.view.shippingAddressCountryValue(),
          name: "Shipping Address Country",
          checks: ["not_empty"],
          dataValue: true,
        },
      ]);

      if (!billingEqualsShipping) {
        fields = fields.concat([
          {
            field: self.view.billingFullName,
            name: "Billing Full Name",
            checks: ["not_empty", "min_length"],
          },
          {
            field: self.view.guestBillingAddressField,
            name: "Billing Address",
            checks: ["not_empty"],
          },
          {
            field: self.view.billingAddressCity,
            name: "Billing Address City",
            checks: ["not_empty"],
          },
          {
            field: self.view.billingAddressState,
            name: "Billing Address State",
            checks: ["not_empty"],
          },
          {
            field: self.view.billingAddressPostalCode,
            name: "Billing Address Postal Code",
            checks: ["not_empty"],
          },
          {
            field: self.view.billingAddressCountryValue(),
            name: "Billing Address Country",
            checks: ["not_empty"],
            dataValue: true,
          },
        ]);
      }
    }

    //	Add email field for users that are logged in.
    //	TODO: Update this eventually so email address is saved with the rest of the user info
    if (self.model.user) {
      fields = fields.concat([
        {
          field: self.view.emailLoggedInUser,
          name: "Email",
          checks: ["not_empty", "email"],
        },
      ]);
    }

    fields = fields.concat([
      {
        field: self.view.cardNumberInput,
        name: "Card Number",
        checks: ["not_empty", "card_num"],
      },
      {
        field: self.view.cardExpirationInput,
        name: "Card Expiration Date",
        checks: ["not_empty"],
      },
      {
        field: self.view.cardCVCInput,
        name: "Card Security Code",
        checks: ["not_empty"],
      },
    ]);

    var verification = self.model.formUtil.verify(fields);

    if (verification.status === "invalid") {
      this.showError(verification.message);
      verification.input.classList.add("error");
      return;
    }

    //	Set and verify addresses
    var shippingAddress, billingAddress, email;
    var shippingIsVerified = true;
    var billingIsVerified = true;
    const addressesToSave = [];

    if (shippingAddressValue) {
      // user is logged in
      shippingAddress = self.addressById(self.model.userMetadata.addresses, shippingAddressValue.value);
      //shippingAddress.name = self.view.userFullName.value;	// add name field
      if (billingEqualsShipping) {
        billingAddress = shippingAddress;
      } else {
        billingAddress = {
          id: this.guid(),
          address: self.view.guestBillingAddressField.value,
          city: self.view.billingAddressCity.value,
          state: self.view.billingAddressState.value,
          zip: self.view.billingAddressPostalCode.value,
          country: self.view.billingAddressCountryValue().dataset.value,
        };
        billingAddress.full_name = self.view.billingFullName.value;

        if (self.view.billingAddressTwo.value) {
          billingAddress.address_two = self.view.billingAddressTwo.value;
        }
      }
    } else {
      shippingAddress = {
        id: this.guid(),
        address: self.view.guestShippingAddressField.value,
        city: self.view.shippingAddressCity.value,
        state: self.view.shippingAddressState.value,
        zip: self.view.shippingAddressPostalCode.value,
        country: self.view.shippingAddressCountryValue().dataset.value,
      };
      shippingAddress.full_name = self.view.userFullName.value;
      shippingAddress.phone = self.view.phone.value || "";

      if (self.view.shippingAddressTwo.value) {
        shippingAddress.address_two = self.view.shippingAddressTwo.value;
      }

      if (billingEqualsShipping) {
        billingIsVerified = shippingIsVerified;
        billingAddress = shippingAddress;
      } else {
        billingAddress = {
          id: this.guid(),
          address: self.view.guestBillingAddressField.value,
          city: self.view.billingAddressCity.value,
          state: self.view.billingAddressState.value,
          zip: self.view.billingAddressPostalCode.value,
          country: self.view.billingAddressCountryValue().dataset.value,
        };
        billingAddress.full_name = self.view.billingFullName.value;

        if (self.view.billingAddressTwo.value) {
          billingAddress.address_two = self.view.billingAddressTwo.value;
        }
      }
      addressesToSave.push(shippingAddress);
      if (!billingEqualsShipping) {
        addressesToSave.push(billingAddress);
      }
    }

    if (shippingIsVerified && !shippingAddress) {
      this.showError("Missing or invalid shipping address.");
      return;
    }
    if (billingIsVerified && !billingAddress) {
      this.showError("Missing or invalid billing address.");
      return;
    }

    var itemData = self.model.getItemsData();
    var div = el;
    try {
      self.view.startLoader(div);
      const { csrf_token, csrf_token_2 } = await this.fetchTokens();

      //	Build form object
      var obj = {
        // 		"name": USER ? USER.displayName : document.querySelector("#full-name").value,
        email: self.model.user ? self.view.emailLoggedInUser.value : self.view.email.value,
        phone: self.view.phoneNumber.value,
        card_number: btoa(self.view.cardNumberInput.value),
        card_expiration_date: btoa(self.view.cardExpirationInput.value),
        card_security_code: btoa(self.view.cardCVCInput.value),
        shipping_address: shippingAddress,
        billing_address: billingAddress,
        items: itemData.items,
        total_cost: itemData.total,
        address_same: billingEqualsShipping,
        csrf_token, // page variable
        csrf_token_2, // page variable
        cssid: "3u8kfk8rej7cvflqqkskura2nt", // page variable
      };

      await self.savingOrderHandler(el, obj, addressesToSave);
    } catch (e) {
      console.log("catch fail");
      console.log(e);
      self.showError("An error occurred.");
    } finally {
      div.value = "Place Order";
      div.textContent = "Place Order";
    }
  }

  async savingOrderHandler(loaderDiv, obj, addressesToSave = []) {
    if (!loaderDiv || !obj) return;

    const { data } = await this.model.saveOrder(JSON.stringify(obj));
    
    if ( isNil( data ) ) return;

    const { data: { message }, status } = JSON.parse( data );

    FCBH.saveToStorage(FCBH.CHECKOUT_FORM, {
      ...obj,
      card_expiration_date: null,
      card_number: null,
      card_security_code: null,
      card_last_digits: atob(obj.card_number).substr(atob(obj.card_number).length - 4),
    });

    if (status === 'success'){
      try {
        if (addressesToSave.length > 0) await this.model.saveAddressToFirebase(addressesToSave);
      } catch (err) {
        console.error(err);
      } finally {
        this.model.clearCart();
        window.location = "/order-confirmation1";
      }
    } else {
      const errorMessage = ['pending', 'decline'].includes( status )
        ? message 
        : 'An error occurred. Contact Thomas at tcummings@fcbhmail.org.';

      this.showError( errorMessage );
    }
  }
}
